import React from 'react';

const Note = (props) =>{
    const deleteNote =() =>{
        props.deleteItem(props.id);
    };
    return <>          
             <div className="col-md-3 overflow-auto col-sm-6 d-inline-block flex-row justify-content-center shadow-lg rounded py-2 border border-danger">
                     <h4 className="text-danger text-center fw-bold text-capitalize">{props.title}</h4>
                     <p className="text fst-italic px-2 "> {props.content} </p>
                     <div className="text-center pt-2">
                     <button className="btn btn-outline-danger shadow" onClick={deleteNote} >Delete Note</button>
                     </div>
             </div>

         
    </>
};

export default Note; 